.auth-popup {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 10;
}
.auth-popup-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 11;
}
.auth-popup-window {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 800px;
  height: 570px;
  background-color: white;
  z-index: 15;
}
.auth-popup-window-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}
.auth-popup-window-h2 {
  width: 100%;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  line-height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: rgba(0, 0, 0, 0.8);
}
.auth-popup-window-input-lable {
  display: flex;
  flex-direction: column;
  padding-left: 60px;
}
.auth-popup-window-input {
  margin: 0;
  padding: 0;
  width: 555px;
  height: 80px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  color: #000000;
  border-top: 0;
  border-left: 0;
  border-right: 0;
}
.auth-popup-window-lable {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  display: flex;
  align-items: center;
  color: rgba(0, 0, 0, 0.6);
}
.auth-popup-window-controls {
  padding-left: 60px;
  padding-right: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  bottom: 0;
  height: 105px;
  width: 100%;
  background-color: #4FBE79;
  box-sizing: border-box;
}
.auth-popup-window-cancel-btn{
  width: 250px;
  height: 60px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 26px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #FFFFFF;
  background: #E53935;
  border: 2px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 5px;
  cursor: pointer;
}
.auth-popup-window-login-btn {
  width: 250px;
  height: 60px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 26px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #FFFFFF;
  background: #4FBE79;
  border: 2px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 5px;
  cursor: pointer;
}
.auth-popup-window-information {
  padding-top: 15px;
  padding-bottom: 15px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  width: 100%;
  text-align: center;
}
.auth-popup-window-important {
  color: #E53935;
}