.container-for-words-and-categories {
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fill, 360px);
  grid-template-rows: 460px;
  gap: 30px;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 98px;
  }
  .back-to-categories {
    font-family: Roboto;
    padding-left: 20px;
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    line-height: 26px;
    list-style-type: none;
  }