.category-card-view {
   position: relative;
   height: 245px;
   width: 360px;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   align-items: center;
   border-style: solid;
   border-width: 2px;
   border-color: #e5e5e5;
   border-radius: 5px;
   padding: 25px 20px 25px 20px;
   box-sizing: border-box;
}
.category-delete {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 40px;
  height: 40px;
  cursor: pointer;
  background: url(../../../assets/delete.svg) center no-repeat;
}
.category-card-view-h2 {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 22px;
  line-height: 26px;
  color: #000000;
}
.category-card-view-p {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  color: #000000;
}
.category-card-view-constrols {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.category-card-view-button {
  background-color: white;
  border-radius: 5px;
  border-style: solid;
  border-width: 2px;
  border-color: #00bf82;
  width: 140px;
  height: 35px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  color: #00BF82;
  padding-top: 5px;
  padding-bottom: 5px;
  box-sizing: inherit;
}
.category-card-view-add-words {
  background-color: white;
  border-radius: 5px;
  border-style: solid;
  border-width: 2px;
  border-color: #00bf82;
  width: 140px;
  height: 35px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 26px;
  color: #00BF82;
  padding-top: 5px;
  padding-bottom: 5px;
}
