.category-card-add {
  width: 360px;
  height: 245px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-style: solid;
  border-width: 2px;
  border-radius: 5px;
  border-color: #e5e5e5;
  box-sizing: border-box;
  cursor: pointer;
}
.create-new-category-h2 {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 22px;
  color: #000000;
}
.create-new-category-plus {
  width: 85px;
  height: 85px;
  background: url(../../../assets/plus-for-category.svg) center no-repeat;
  background-size: cover;
}