.admin-header {
  display: flex;
  justify-content: space-between;
  background-color: #00bf82;
  height: 100px;
  align-items: center;
}
.admin-link {
  font-family: Roboto;
  padding-left: 20px;
  font-style: normal;
  font-weight: bold;
  font-size: 22px;
  line-height: 26px;
  list-style-type: none;
}
.admin-link .active{
  pointer-events: none;
}
.admin-nav {
  display: flex;
}
.admin-link-cat a {
  text-decoration: none;
  pointer-events: none;
}
.logout {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 22px;
  line-height: 26px;
  padding-right: 20px;
}
.admin-link-word {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 22px;
  line-height: 26px;
  list-style-type: none;
}
.category-edit {
  text-decoration: underline;
}