.category-card-editor {
  position: relative;
  height: 245px;
  width: 360px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  border-style: solid;
  border-width: 2px;
  border-color: #e5e5e5;
  border-radius: 5px;
  padding: 25px 20px 25px 20px;
  box-sizing: border-box;
}
.category-delete {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 40px;
  height: 40px;
  cursor: pointer;
  background: url(../../../assets/delete.svg) center no-repeat;
}
.category-card-editor-constrols {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.category-card-editor-button {
  padding: 10px 20px 10px 20px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #00BF82;
  background-color: white;
  outline: 0;
  border-style: solid;
  border-color: #00BF82;
  border-radius: 5px;
  cursor: pointer;
}
.category-card-editor-button.red {
  color: #E53935;
  border-color: #E53935;
}
.category-card-editor-form {
  padding-top: 25px;
  display: flex;
  flex-direction: column;
}
.category-card-editor-form-label {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 9px;
  line-height: 11px;
  display: flex;
  align-items: center;
  color: #3F51B5;
}
.category-card-editor-form-input {
  width: 290px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  color: #000000;
  border-top-width: 0;
  border-right-width: 0;
  border-left-width: 0;
  border-bottom-width: 1px;
  border-color: #3F51B5;
}